import React, { useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import axios from 'axios'
import AdminUsersTable from './AdminUsersTable';
import AdminEmailTable from './AdminEmailsTable';
import AdminPositionsControl from './AdminPositionsControl';

export default function AdminPanel() { //   
  const [view, setView] = useState(''); //     

  const handleViewChange = (value) => { //      (, , )
    setView(value); //     
  }

  return (
    <div>
      <div>
        <Button
          variant="contained"
          color="primary"
          style={{
            fontSize: '15px',
            position: 'relative',
          }}
          onClick={() => handleViewChange('messages')} //    
        >
          View Messages
          </Button>
        <Button
          variant="contained"
          color="primary"
          style={{
            fontSize: '15px',
            position: 'relative',
            left: '40px'
          }}
          onClick={() => handleViewChange('users')} //    
        >
          View Users
          </Button>
        <Button
          variant="contained"
          color="primary"
          style={{
            fontSize: '15px',
            position: 'relative',
            left: '80px'
          }}
          onClick={() => handleViewChange('positions')} //    
        >
          View All Positions
          </Button>
      </div>
      <div>
        {/*          */}
        {view === 'messages' && <AdminEmailTable />}
        {/*           */}
        {view === 'users' && <AdminUsersTable />}
        {view === 'positions' && <AdminPositionsControl />}
      </div>
    </div>
  )
};

